.Search {
    position: relative;
    width: 30%;
    height: 35px;
    margin: 0 auto;
    padding: 0 20px;
}

@media (max-width: 700px) {
    .Search {
        width: 100%;
    }
}

.Search-icon {
    z-index: 1;
    position: absolute;
    top: 9px;
    left: 28px;
    background-image: url('./search.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 18px;
    height: 18px;
}

.Search-input {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #1f364d;
    border-radius: 4px;
    border: 0;
    padding-left: 35px;
    color: white;
    opacity: .8;
    -webkit-transition: opacity .2s;
    -o-transition: opacity .2s;
    transition: opacity .2s;
    width: 100%;
    height: 35px;
}

.Search-input:focus {
    outline: none;
    opacity: 1;
}

.Search ::-webkit-input-placeholder {
    color: #9cb3c9;
    opacity: 1;
}

.Search :-ms-input-placeholder {
    color: #9cb3c9;
    opacity: 1;
}

.Search ::-ms-input-placeholder {
    color: #9cb3c9;
    opacity: 1;
}

.Search ::placeholder {
    color: #9cb3c9;
    opacity: 1;
}

.Search-loading {
    position: absolute;
    top: 9px;
    right: 28px;
}

.Search-result-container {
    position: relative;
    width: 100%;
    max-height: 299px;
    overflow-y: auto;
    background-color: #0f273d;
    border: 1px solid #0c2033;
    border-radius: 4px;
    -webkit-box-shadow: 0px 0px 40px 0px#1f364d;
            box-shadow: 0px 0px 40px 0px#1f364d;
    margin-top: 10px;
}

.Search-result {
    color: #9cb3c9;
    padding: 15px 0 15px 35px;
    border-bottom: 2px solid #0c2033;
    cursor: pointer;
}

.Search-result:hover {
    color: #fff;
}

.Search-no-result {
    color: #9cb3c9;
    padding: 15px 0 15px 35px;
    border-bottom: 1px solid #0f273d;
}



