/*
  Application colors
  text:               #fff;
  text-secondary:     #9cb3c9;
  error-red:          #d64d96;
  green:              #3cd483;
  indigo-light:       #1f364d;
  indigo-normal:      #0f273d;
  indigo-dark:        #0c2033;
  blue-normal:        #4997e5;
  blue-dark:          #457cb2;
*/

/* Load Open Sans font from Google Fonts */

@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');
/*! normalize.css v7.0.0 | MIT License | github.com/necolas/normalize.css */

button,
hr,
input {
    overflow: visible
}

audio,
canvas,
progress,
video {
    display: inline-block
}

progress,
sub,
sup {
    vertical-align: baseline
}

[type=checkbox],
[type=radio],
legend {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0
}

html {
    line-height: 1.15;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%
}

body {
    margin: 0
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section {
    display: block
}

h1 {
    font-size: 2em;
    margin: .67em 0
}

figure {
    margin: 1em 40px
}

hr {
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    height: 0
}

code,
kbd,
pre,
samp {
    font-family: monospace, monospace;
    font-size: 1em
}

a {
    background-color: transparent;
    -webkit-text-decoration-skip: objects
}

abbr[title] {
    border-bottom: none;
    text-decoration: underline;
    -webkit-text-decoration: underline dotted;
            text-decoration: underline dotted
}

b,
strong {
    font-weight: bolder
}

dfn {
    font-style: italic
}

mark {
    background-color: #ff0;
    color: #000
}

small {
    font-size: 80%
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative
}

sub {
    bottom: -.25em
}

sup {
    top: -.5em
}

audio:not([controls]) {
    display: none;
    height: 0
}

img {
    border-style: none
}

svg:not(:root) {
    overflow: hidden
}

button,
input,
optgroup,
select,
textarea {
    font-family: sans-serif;
    font-size: 100%;
    line-height: 1.15;
    margin: 0
}

button,
select {
    text-transform: none
}

[type=reset],
[type=submit],
button,
html [type=button] {
    -webkit-appearance: button
}

[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner,
button::-moz-focus-inner {
    border-style: none;
    padding: 0
}

[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring,
button:-moz-focusring {
    outline: ButtonText dotted 1px
}

fieldset {
    padding: .35em .75em .625em
}

legend {
    color: inherit;
    display: table;
    max-width: 100%;
    white-space: normal
}

textarea {
    overflow: auto
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
    height: auto
}

[type=search] {
    -webkit-appearance: textfield;
    outline-offset: -2px
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
    -webkit-appearance: none
}

::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit
}

summary {
    display: list-item
}

[hidden],
template {
    display: none
}

body {
    background-color: #0c2033;
    font-family: 'Open Sans', sans-serif;
    color: #fff;
    letter-spacing: .5px;
    font-size: 16px;
    font-weight: 300;
}

.percent-raised {
    color: #3cd483;
}

.percent-fallen {
    color: #d64d96;
}

.loading-container {
    width: 100%;
    text-align: center;
    margin: 40px auto;
}

.error {
    width: 100%;
    margin: 40px 0;
    text-align: center;
    color: #d64d96;
}

/* Remove close icon from input on IE */

input::-ms-clear {
    display: none;
}



